body {
  font-family: cursive;
  line-height: 1.5;
}

.timer {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  text-align: center;
}

.field {
  margin: 10px;
  display: flex;
  flex-direction: column;
  width: 130px;
}

.value {
  padding: 10px;
  font-size: 50px;
  color: maroon;
  background-color: burlywood;
  border-radius: 25%;
}

.label {
  font-size: 30px;
  color: firebrick;
}
